<template>
    <div class="about" @mousemove="cursorchange2()">
        <div class="cursor2">
            <div></div>
        </div>
        <HEADER logo="true" color="white" @showMenu="show"></HEADER>
        <div class="hidemenu">
            <hidemenu @closeMenu="close"></hidemenu>
        </div>
        <about></about>
        <div v-if="isloading">
            <loadingpage></loadingpage>
        </div>
    
    </div>
</template>

<script>
import HEADER from '../components/Header'
import hidemenu from '../components/HideMenu'
import gsap from "gsap";
import loadingpage from '../components/Loadingpage'
import about from '../components/about'
export default {
    data() {
        return {
            isloading: true
        }
    },
    mounted() {
        setTimeout(this.closeloading, 4000);
    },
    methods: {
        show() {
            gsap.to('.hidemenu', {
                left: 0,
                opacity: 1,
                duration: 0.5
            })
        },
        close() {
            gsap.to('.hidemenu', {
                left: '100%',
                opacity: 0,
                duration: 0.4
            })
        },
        closeloading() {
            this.isloading = false
        },
        cursorchange2() {
            const cursor = document.querySelector('.cursor2');
            cursor.setAttribute("style", "top: " + (event.pageY - 10) + "px; left: " + (event.pageX - 10) + "px;")

        }
    },
    components: {
        HEADER,
        hidemenu,
        loadingpage,
        about
    }
}
</script>

